@import url(https://fonts.googleapis.com/css?family=Caveat|Lobster|Quicksand|Amatic+SC&display=swap);
#home{
  position: absolute;
  margin: 0px;
  overflow: hidden;
  top: 0px;
  bottom: 0px;
  width: 100%;
}
.full {
  display: grid;
  grid-template-rows: repeat (5, 1fr);
  grid-template-columns: auto-fit 1fr 1fr 1fr 600px;
  grid-gap: 15px;
  font-size: 250%;
  font-family: 'Amatic SC', cursive;
  text-align: center;
  margin: 20px 0 20px 20px;
}
.full a:not(.welcome):not(.home) {
  line-height: 30vh;
}
.full > a,
.iconsnav:not(.welcome) {
  /* border-left: 4px black solid;
  border-top: 4px black solid; */
  color: gold;
  text-shadow: 2px 2px 2px grey;
  font-size:50px;
  padding:10px;
  /* border-radius:50%; */
}
.full a:not(.welcome):hover {
  color: black;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.full i:hover {
  color: black;
  -webkit-transition: color 1s;
  transition: color 1s;
}

.home {
  grid-area: 1 / 1 / 4 / 1;
  line-height: 60vh;
}
.rose {
  grid-area: 1 / 4 / 5 / 5;
}
.rose img {
  width: 80vh;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  text-align: right;
  background-image: url(/portfolio/static/media/final824-roses-drawing.9c0c16ad.svg);
  background-size: cover;
  background-position: left;
  display: grid;
  place-items: center;
  border-left: none !important;
  text-shadow: 2px 2px black;
}
nav p {
  font-size: 1.5em;
  text-align: center;
  padding:5%;
}
.iconsnav {
  border: none;
  font-size: 1em;
  grid-area: 4 / 1 / 4 / 4;
  display: inline;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.iconsnav i {
  color: gold;
}
.iconsnav a:last-child{
  color:gold;
  font-family: arial;
  font-weight: bold;
}

.about {
  padding: 50px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}
.about p {
  font-family: "Quicksand", sans-serif;
  /* border-top: 1px solid gold;
  border-left: 1px solid gold; */
  text-align: justify;
  font-size:1.5em;

}
.about > p:nth-of-type(-n + 2) {
  /* margin-bottom: 30px; */
  padding: 70px 50px 50px 50px;
}
.date p {
  background-color: gold;
  width: 80px;
  height: 80px;
  line-height: 80px;
  border: 4px gold solid;
  border-radius: 50%;
  font-size: 3em;
}
.date {
  font-family: "Lobster", cursive;
  padding: 15px 30px 15px 20px;
  float: right;
  shape-outside: circle(50%);
}
.skills {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  word-spacing: 10px;
}
.skills i {
  margin: 10px;
  font-size:3.5em;
}

.projects {
  padding: 70px 50px 50px 50px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

h2 {
  font-size: 166%;
  padding-bottom: 15px;
  font-family: "Lobster", cursive;
  color: gold;
}
h3 {
  font-family: "Quicksand", sans-serif;
}
.imagecenter {
  padding: 15px;
  text-align: center;
}
.projects img {
  padding-top: 70px;
  max-width: 100%;
  max-height: 100%;
}
.projects ul {
  margin-left: 15px;
  padding: 15px 15px 0;
  /* border-top: 1px solid gold;
  border-left: 1px solid gold; */
}
.projects ul:nth-of-type(2) {
  margin-bottom: 0;
}
.projects li {
  padding-bottom: 5px;
  font-family: "Quicksand", sans-serif;
}
.projects li:nth-child(6) {
  font-size: 1.5em;
  padding-top: 15px;
  border-bottom: 1px solid gold;
}
.projects i {
  padding: 15px;
}
.projects span {
  padding-left: 2%;
}
.button:hover > span,
i {
  color: gold;
  border: gold;
}

.contact {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
  font-family: "Quicksand", sans-serif;
  padding: 50px;
}
form {
  margin-left: 15px;
}
input,
textarea {
  width: 40vw;
  border: 2px solid transparent;
  margin: 5px;
  padding: 5px;
  background-color: transparent;
  color: gold;
  font-size: 1.3em;
  font-family: "Quicksand", sans-serif;
}
textarea {
  height: 20vw;
}
.contact ul li:nth-child(1n + 1) {
  border-bottom: 1px solid gold;
}
.avatar {
  align-self: center;
  margin: 0 auto;
  background-image: url(/portfolio/static/media/myAvatar.97a8dc08.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 21vw;
  width: 21vw;
  border-radius: 50%;
  border: 3px solid white;
  text-align: center;
  margin-bottom: 25px;
}
h4 {
  display: inline-block;
  color: gold;
  font-size: 2em;
  position: relative;
  top: 101%;
  text-shadow: 2px 2px 2px black;
  font-family: "Amatic SC", cursive;
  line-height: 50px;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::-moz-placeholder,
textarea::-moz-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::-ms-input-placeholder,
textarea::-ms-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::placeholder,
textarea::placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input[value="send"]:hover {
  color: gold;
  -webkit-transition: color 1s;
  transition: color 1s;
}

.goback {
    font-size: 2em;
  }
  .navstyle {
    padding: 50px 30px 30px 30px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    text-align: center;
  }
  .navstyle i {
    color: grey;
    font-size: 2em;
    padding:5px;
  }
  .navstyle a {
    padding: 0 3% 0 3%;
  }
  .navstyle i:hover {
    color: gold;
    -webkit-transition: color 1s;
    transition: color 1s;
  }
@media screen and (max-width: 768px) {
  body {
    -ms-scroll-chaining: chained;
        overscroll-behavior: auto;
  }
  .responsive {
    height:100%;
    width: 100%;
    display: inline-grid;
    grid-template-columns: auto auto;
    grid-template-rows:auto auto;
  align-items: center;
  }
  .resFooter,.resWelcome,.iconsnav{
    border: 1px solid gold;
    padding: 35px;
    height: 100%;
  }
  h1, .resFooter {
    font-size: 3em;
  }
  .resFooter{
      grid-area: 1/1/1/1;
      align-self:center;
    }
    .resFooter li{
      padding: 20% 0 20% 0;
    }
    .resFooter i {
      color: gold;
      text-shadow: 2px 2px 2px grey;
    }
    .resWelcome {
      grid-area: 1/2/1/2;
      font-size: 8vh;
      text-align: end;
      text-shadow: 1px 1px black;
      font-family: "Amatic SC", cursive;
    }
    .iconsnav{
      grid-area: 2/1/2/3;
      line-height: 3em;

    }
    .about,
    .projects {
      padding: 0 0 30px 0;
    }
    .projects li:nth-child(6) {
      font-size: 2em;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      padding: 30px;
    }
    .projects li a {
      padding: 15px;
      border: 2px solid white;
      border-radius: 50%;
      width: 80px;
      height: 80px;
      display: grid;
      place-content: center;
    }
    ul span {
      display: none;
    }
  }
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
text-rendering: optimizeLegibility !important;
-webkit-font-smoothing: antialiased !important;
}
*:before,
*:after {
 box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/portfolio/static/media/background.d891b24c.jpg) no-repeat center center fixed;
}
/* border page */
#top, #bottom, #left, #right {
  background: whitesmoke;
  position: fixed;
  z-index:1;
	}
	#left, #right {
		top: 0; bottom: 0;
		width: 5px;
		}
		#left { left: 0; }
		#right { right: 0; }
		
	#top, #bottom {
		left: 0; right: 0;
		height: 5px;
		}
		#top { top: 0; }
    #bottom { bottom: 0; }
/* border page end*/

.projects,
.about,
.contact {
  margin: 0 50px 70px 50px;
  background-color: rgba(0, 0, 0, 0.4);
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}
.center{
  text-align:center;
}
.divider{
  width: 111px;
  display: inline-block;
  border: none;
  border-bottom: 5px solid gold;
}
h1 {
  font-size: 4em;
  color: white;
  text-align:center;
  font-family: 'Caveat', cursive;

}
h3,
p,
i,
span {
  color: white;
}

/* animation */

.puff-in-center{-webkit-animation:puff-in-center .7s cubic-bezier(.47,0.000,.745,.715) both;animation:puff-in-center .7s cubic-bezier(.47,0.000,.745,.715) both}

/* keyframes */

@-webkit-keyframes puff-in-center{0%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(4px);filter:blur(4px);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0);filter:blur(0);opacity:1}}

@keyframes puff-in-center{0%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(4px);filter:blur(4px);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0);filter:blur(0);opacity:1}}

.App-header {
  font-size: calc(5px + 2vmin);
}


